    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body {
    position: relative;
    top: 60px;
    height: calc(100vh - 60px);
    padding-top: 100px;
    overflow: auto;
    text-align: center;
    flex-direction: column;
    color: #fff;
    background-color: #282c34;
}

.body h1 {
    margin-bottom: 50px;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f0f0f0;
    padding: 10px;
    width: 100%;
    height: 70px;
    position: fixed;
    z-index: 1;
}

.nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.menu {
    display: flex;
    align-items: center;
}

.menu-button {
    border: none;
    background-color: transparent;
    font-size: 24px;
    cursor: pointer;
}

.menu-content {
    position: absolute;
    top: 70px;
    left: 0;
    width: 250px;
    height: 100vh;
    background-color: #f0f0f0;
    overflow-y: auto;
    transition: transform 0.3s ease-in-out;
}

.menu-content.open {
    transform: translateX(0);
}

.menu-content.close {
    transform: translateX(-100%);
}

.categorias-list {
    list-style: none;
    padding: 0;
}

.categorias-list li {
    padding: 10px;
}

.categorias-list li a {
    text-decoration: none;
}

.categorias-list li.selected {
    background-color: #0D6EFD;
}

.categorias-list li.selected a{
    color: #fff;
}

.menu-list {
    list-style: none;
    padding: 0;
}

.menu-item {
    padding: 10px;
}

.buttons button, .buttons a {
    margin-left: 10px;
}

.row .cart-item {
    display: flex;
    align-items: center;
    padding: 10px;
}

.row .cart input {
    width: 60px;
}

.card {
    border: none;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card-title {
    font-size: 1.25rem;
    font-weight: 500;
}

.card-text {
    margin-bottom: 0.5rem;
}

.btn-primary {
    background-color: #007bff;
    border-color: #007bff;
}

.image-placeholder {
    width: 150px;
    height: 150px;
    background-color: lightgray;
    display: block;
    align-items: center;
    justify-content: center;
}

.image-text{
    color: black;
    font-size: 16px;
}

.popup {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
}

.popup-content {
    background-color: #f9f9f9;
    color: black;
    padding: 20px;
    border: 1px solid #888;
    width: fit-content;
    border-radius: 20px;
}

.popup .close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    &:hover,
    &:focus {
        color: black;
        text-decoration: none;
        cursor: pointer;
    }
}

.form {
    border: 1px solid #ccc;
    padding: 20px;
    margin: 10px;
    border-radius: 5px;
    width: 100%;
}

.row label {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5px
}

.row input {
    width: calc(100% - 16px);
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.popup .popup-content.checkout{
    width: 50%;
}

.popup .popup-content.checkout .row {
    padding: 10px;
    justify-content: center;
    align-items: center;
}

.popup .popup-content.checkout h5 {
    margin-top: 5%;
    margin-left: 5%;
    text-align: left;
}
.popup .popup-content.checkout .row.user-info,
.popup .popup-content.checkout .row.cart {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 20px;
    margin: 20px;
}

.popup .popup-content.checkout .row.cart {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.popup .popup-content.checkout .row.user-info {
    text-align: left;
}

.flags-container {
    background-size: cover;
    background-position: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.flags-container h1 {
    margin-bottom: 20px;
    color: #fff;
}

.flags-container input {
    margin-bottom: 10px;
    margin-right: 20px;
    width: 600px;
}

.search input {
    width: 50vh;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.popup .popup-content.eye{
    width: 60vh;
    height: 60vh;
}`, "",{"version":3,"sources":["webpack://./src/index.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT;;cAEU;IACV,mCAAmC;IACnC,kCAAkC;AACtC;;AAEA;IACI;aACS;AACb;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,0BAA0B;IAC1B,kBAAkB;IAClB,cAAc;IACd,kBAAkB;IAClB,sBAAsB;IACtB,WAAW;IACX,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,yBAAyB;IACzB,aAAa;IACb,WAAW;IACX,YAAY;IACZ,eAAe;IACf,UAAU;AACd;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,6BAA6B;IAC7B,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,OAAO;IACP,YAAY;IACZ,aAAa;IACb,yBAAyB;IACzB,gBAAgB;IAChB,sCAAsC;AAC1C;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,gBAAgB;IAChB,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,wCAAwC;AAC5C;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,2BAA2B;IAC3B,cAAc;IACd,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,oCAAoC;IACpC,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB;;QAEI,YAAY;QACZ,qBAAqB;QACrB,eAAe;IACnB;AACJ;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB;AACJ;;AAEA;IACI,wBAAwB;IACxB,YAAY;IACZ,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;AACA;;IAEI,sBAAsB;IACtB,kBAAkB;IAClB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;AAC3B;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;IACtB,2BAA2B;IAC3B,aAAa;IACb,aAAa;IACb,sBAAs